@import url('https://fonts.googleapis.com/css2?family=Prompt&display=swap');

:root {
    --light-grey: #D5DBD9;
    --normal-grey: #979C9A;
    --darker-grey: #595959;
    --yellow: #FED865;
    --light-orange: #E8AC51;
    --orange: #FF9559;
    --coral: #FE4D47;
    --dark-coral: #AD2A2A;
    --dark-red: #6B0A10;
    --green: #3DC223;
    --light-green: #92E864;
    --leaf-green: #6DBA4B;
    --dark-green: #3B7A3B;
    --font-prompt: 'Prompt', sans-serif;
}

button {
    background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

input {
    background: none;
    border: none;
    outline: none;
}

.container {
    width: 100%;
    display: flex;
    justify-content: center;
}

[class*="keyboard-row"] {
    display: flex;
    justify-content: center;
    gap: 3px;
    flex-basis: 100%;
    margin: 3px;
}

.keyboard {
    width: 700px;
}


.LetterButton {
    width: 60px;
    height: 60px;
    background-color: var(--light-grey);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: var(--font-prompt);
    box-shadow: inset 0 0 0 3px var(--darker-grey);
    font-size: 25px;
}

.LetterButton:hover {
    background-color: var(--normal-grey);
    transition: 0.3s;
}

.YellowButton {
    background-color: var(--yellow);
    box-shadow: inset 0 0 0 3px var(--light-orange);
    color: white;
}

.YellowButton:hover {
    background-color: var(--orange);
}

.RedButton {
    background-color: var(--coral);
    box-shadow: inset 0 0 0 3px var(--dark-red);
    color: white;
}

.RedButton:hover {
    background-color: var(--dark-coral);
}

.GreenButton {
    background-color: var(--light-green);
    box-shadow: inset 0 0 0 3px var(--dark-green);
    color: white;
}

.GreenButton:hover {
    background-color: var(--leaf-green);
}

.correctLettersPanel {
    display: flex;
    justify-content: center;
    margin-top: 40px;
    gap: 3px;
}

.correctInput {
    width: 60px;
    height: 60px;
    border-radius: 5px;
    box-shadow: inset 0 0 0 3px var(--green);
    text-align: center;
    font-size: 25px;
    font-family: var(--font-prompt);
}

.resultsPanel {
    margin-top: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
}

.resultsHeader {
    font-family: var(--font-prompt);
    display: flex;
    margin-top: 50px;
    justify-content: center;
    flex-basis: 100%;
}

.results {
    flex-basis: 70%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.resultsInfo {
    flex-basis: 100%;
    display: flex;
    justify-content: center;
    font-family: var(--font-prompt);
}

.result {
    flex-basis: 10%;
    font-size: 20px;
    font-family: var(--font-prompt);
    display: flex;
    justify-content: center;
    padding: 10px;
}